import { Location } from '@angular/common';
import { Component } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { APP_ROUTE } from '@core/constants';
import { ENQUIRY_DETAIL_ACTIONS } from '@core/constants/agent-hub-actions.constant';
import { AgentHubActions } from '@core/enums/agent.enum';
import { AgentHubAction, Inquiry } from '@core/models';
import { SnackService } from '@core/services/snack.service';
import { WindowService } from '@core/services/window.service';
import { AgentHubFacade } from '@feature/agent-hub/agent-hub.facade';
import { ActionsComponent } from '@feature/agent-hub/components/actions/actions.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { finalize } from 'rxjs';

@Component({
    selector: 'findqo-enquiry-detail',
    templateUrl: './enquiry-detail.page.html',
    styleUrl: './enquiry-detail.page.scss',
    standalone: false
})
export class EnquiryDetailPage {
	constructor(
		protected readonly agentHubFacade: AgentHubFacade,
		private readonly window: WindowService,
		private readonly bottomSheet: MatBottomSheet,
		private readonly sharedAdsFacade: SharedAdsFacade,
		private readonly snack: SnackService
	) {}
	readonly actionOption: (enquiry: Inquiry) => AgentHubAction[] = ENQUIRY_DETAIL_ACTIONS;
	isLoading = false;
	loaderRef = null;
	readonly appRoute = APP_ROUTE;

	openActionDialog(enquiry: Inquiry): void {
		const ref = this.bottomSheet.open(ActionsComponent, {
			panelClass: ['agent-ad-detail'],
		});

		ref.instance.ad = enquiry?.ad;
		ref.instance.isBottomActionbar = true;
		ref.instance.dialogRef = ref;
		ref.instance.options = this.actionOption(enquiry);

		ref.afterDismissed().subscribe((res) => {
			if (res?.reference) {
				this.handleActionButton(res, enquiry);
			}
		});
	}

	handleActionButton(actionItem: AgentHubAction, enquiry: Inquiry): void {
		if (actionItem.reference) {
			this.isLoading = true;
			this.loaderRef = actionItem.reference;
		}

		switch (actionItem.reference) {
			case AgentHubActions.VIEW_TENANCY_RESUME:
				this.sharedAdsFacade
					.getPdfLocalUrl(enquiry.tenantin?.tenantResumeUrl)
					.pipe(
						finalize(() => {
							this.isLoading = false;
						})
					)
					.subscribe((res) => {
						const LINK = URL.createObjectURL(res);
						this.window.reference.open(LINK);
					});
				break;

			case AgentHubActions.VIEW_AD_ON_WEB:
				this.openNewWindow(actionItem.link);
				this.isLoading = false;
				break;

			case AgentHubActions.MARK_AS_PENDING:
			case AgentHubActions.MARK_AS_COMPLETED:
				this.agentHubFacade.markAsPending(enquiry?.id, { completionStatus: actionItem.value });
				break;

			default:
		}
	}

	openNewWindow(link: string): void {
		const OPENED_TAB = this.window.reference.open(link, '_blank');

		// If popup window is disabled from the settings
		if (!OPENED_TAB || OPENED_TAB.closed || typeof OPENED_TAB.closed === 'undefined') {
			this.snack.open({
				data: { translationKey: 'n3965', type: 'error' },
				panelClass: ['snackbar-error'],
			});
		}
	}
}
