import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { EnquiryDetailPage } from './enquiry-detail.page';
import { EnquiryDetailResolver } from '@feature/agent-hub/resolvers/enquiry-detail.resolver';

const routes: Routes = [
	{
		path: ':enquiryId',
		component: EnquiryDetailPage,
		resolve: { adResolver: EnquiryDetailResolver },
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class EnquiryDetailPageRoutingModule {}
