import { isPlatformBrowser } from '@angular/common';
import { inject, PLATFORM_ID } from '@angular/core';
import { ActivatedRouteSnapshot, ResolveFn, Router, RouterStateSnapshot } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { EnquiryState } from '@core/data-access/states/enquiry.state';
import { Inquiry } from '@core/models';
import { Observable, catchError, finalize, first, map, of, pipe, tap } from 'rxjs';

export const EnquiryDetailResolver: ResolveFn<any> = (
	route: ActivatedRouteSnapshot,
	_state: RouterStateSnapshot,
	adsApi: AdsApi = inject(AdsApi),
	enquiryState = inject(EnquiryState),
	router = inject(Router),
	platformId = inject(PLATFORM_ID)
): Observable<any> => {
	enquiryState.setLoading(true);

	if (isPlatformBrowser(platformId)) {
		return adsApi.getEnquiryById(route.params['enquiryId']).pipe(
			catchError(() => {
				enquiryState.setEnquiry(undefined);
				router.navigate([APP_ROUTE.wildCard], { skipLocationChange: true });
				return of(null);
			}),
			map((res) => res.data),
			tap((enquiry: Inquiry) => {
				enquiryState.setEnquiry(enquiry);
			}),
			finalize(() => enquiryState.setLoading(false))
		);
	}

	return of(undefined);
};
